.homepage {
    display: grid;
    gap: 20vh;
}

.background {
    background-image: url("../imgs/firstpagebg.svg");
    height: 600px;
    background-repeat: no-repeat;
    background-size:cover;
    background-position: center;
    position: relative;
    z-index: 0;
    display: grid;
}

.loginbox {
    width: 90vw;
    background: linear-gradient(90deg, white, rgba(255, 255, 255, 0.562) 90%);
    border: 1px solid #707070;
    padding: 50px 3vw;
    position: absolute;
    bottom: -100px;
    border-radius: 10px;
    justify-self: center;
    backdrop-filter: blur(15px);
    display: grid;
    grid-auto-flow: column;
}


.mob {
    display: grid;
    gap: 10px;
    padding: 10px;
    border-right: 2px solid #323232;
    justify-content: center;
}

.mob-nobdr {
    display: grid;
    gap: 10px;
    padding: 10px;
    justify-content: center;
}

.nineone {
    position: absolute;
    left: 45px;
    border-right: 1px solid black;
    padding-right: 6px;
    font-size: small;
    font-weight: 500;
}

.input {
    padding: 15px;
    margin-left: 35px;
    padding-left: 60px;
    border-radius: 10px;
    border: 1px solid #707070;
    outline: 0px;
}

.input2 {
    padding: 15px;
    margin-left: 35px;
    padding: 15px;
    border-radius: 10px;
    border: 1px solid #707070;
    outline: 0px;
    width: 220px;
}

.mobinp {
    position: relative;
}

.radioimg {

}

.signup {
    display: grid;
}

button {
    background-color: #FFCD6C;
    padding: 20px;
    border: 0px;
    border-radius: 50px;
    font-weight: 500;
    width: 300px;
}

.lim {
    width: 250px;
    text-align: center;
    margin-top: 10px;
    margin-left: auto;
    margin-right: auto;
}

input:hover {
    border: 1px solid rgb(0, 0, 0);
}

button:hover {
    opacity: 0.8;
    cursor: pointer;
}

.otp {
    width: 100vw;
    height: 100vh;
    background-color: #0000008f;
    position: fixed;
    z-index: 2;
    top: 0px;
    display: none;
    align-items: center;
    justify-content: center;
}

.loginotp {
    display: grid;
    grid-auto-flow: column;
    grid-template-columns: 40% 60%;
    background-color: white;
    max-width: 800px;
    margin: 20px;
    border-radius: 10px;
}

.leftopt img {
    justify-self: center;
    max-width: 200px;
}

.leftopt {
    display: grid;
    background-image: url("../imgs/loginoptbg.svg");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    justify-content: center;
    padding: 40px;
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;
}

.leftopt h3 {
    position: relative;
    bottom: -40%;
    color: white;
}

.rightotp {
    padding: 0px;
    display: grid;
    padding: 0px 20px 20px 20px;
}

.otpinput {
    width: 150px;
    padding: 10px;
    outline: 0px;
    border: 0px;
    border-bottom: 1px outset black;
    row-gap: 10px;
    font-size: xx-large;
    letter-spacing: 4vw;
    text-align: center;
    justify-self: center;
}

.rightotp button {
    justify-self: center;
    margin-top: 20px;
    width: 30vw;
    max-width: 400px;
    min-width: 200px;
}

.otpinput:hover {
    border: 0px;
    border-bottom: 1px outset black;
}

@media (max-width: 1160px) {
    .loginbox {
        width: 90vw;
        background: linear-gradient(90deg, white, rgba(255, 255, 255, 0.562) 90%);
        border: 1px solid #707070;
        padding: 50px 3vw;
        position: absolute;
        bottom: 100px;
        border-radius: 10px;
        justify-self: center;
        backdrop-filter: blur(15px);
        display: grid;
        grid-auto-flow: row;
    }

    .mob {
        border: 0px;
        display: grid;
    }

    .input {
        padding: 15px;
        margin-left: 35px;
        padding-left: 60px;
        border-radius: 10px;
        border: 1px solid #707070;
        outline: 0px;
        width: 50vw;
    }

    .input2 {
        padding: 15px;
        margin-left: 35px;
        padding: 15px;
        border-radius: 10px;
        border: 1px solid #707070;
        outline: 0px;
        width: calc(50vw + 50px);
    }

    button {
        background-color: #FFCD6C;
        padding: 20px;
        border: 0px;
        border-radius: 50px;
        font-weight: 500;
        width: 50vw;
        margin-top: 10px;
    }

    .signup {
        transform: translateX(15px);
    }
}