body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.head {
  font-size: xx-large;
}

.subhead {
  font-size: x-large;
}

.sub {
  font-size: small;
}

.flex {
  display: flex;
  gap: 10px;
  align-items: center;
}

.flexr {
  display: flex;
  flex-direction: column;
  gap: 10px;
  align-items: center;
}

.grid {
  display: grid;
  gap: 10px;
  align-items: center;
}

.gridr {
  display: grid;
  grid-auto-flow: column;
  align-items: center;
}

.bordernone {
  border: 0px;
}

.subpage {
  display: grid;
  justify-content: center;
  grid-template-columns: 80vw;
  text-align: center;
}

.subpagehead {
  font-size: xx-large;
  font-weight: 500;
  justify-self: center;
}

.subpagetext {
  font-size: small;
  color: #8B9B9F;
  justify-self: center;
  width: 50vw;
}

.line {
  width: 100px;
  border-bottom: 1px solid #C1945C;
  justify-self: center;
}

.subpageimg {
  width: 60px;
  justify-self: center;
}