.navBar {
    background: radial-gradient(50vw,rgba(0, 0, 0, 0.800), black);
    display: grid;
    grid-auto-flow: column;
    align-items: center;
    color: white;
    padding: 40px 10vw;
    position: sticky;
    top: 0px;
    z-index: 1;
}

.logo {
    width: 12vw;
    min-width: 150px;
}

.login {
    background-color: #4B565F;
    width: 12vw;
    padding: 10px;
    height: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-bottom-right-radius: 10px;
    border-bottom-left-radius: 10px;
    position: relative;
    top: -40px;
    justify-self: right;
    min-width: 200px;
    font-size: x-large;
    font-weight: 700;
}